*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "GraphikLCTT", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #DCF4FE; }

a {
  color: #000000;
  text-decoration: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #FFFFFF;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  margin: 44px 0;
  max-width: 1168px;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  @media (min-width: 768px) {
    .wrapper {
      margin: 64px 36px; } }
  @media (min-width: 1024px) {
    .wrapper {
      margin: 96px 48px; } }
  @media (min-width: 1280px) {
    .wrapper {
      margin: 112px auto; } }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    display: flex;
    flex-direction: column;
    gap: 8px;
    position: relative; }
    @media (min-width: 1024px) {
      .wrapper main {
        flex-direction: row; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.container {
  width: 100%;
  min-width: 320px;
  padding: 40px 20px;
  background-color: #FFFFFF;
  border-radius: 10px; }
  @media (min-width: 768px) {
    .container {
      padding: 32px 120px; } }
  @media (min-width: 1024px) {
    .container {
      padding: 40px; } }
  @media (min-width: 1280px) {
    .container {
      margin: 0 auto;
      max-width: 1280px; } }

@media (min-width: 1024px) {
  .booking {
    display: flex;
    flex-direction: column;
    justify-content: space-between; } }

.booking__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 600;
  margin-bottom: 16px; }
  @media (min-width: 1024px) {
    .booking__title {
      font-size: 32px;
      line-height: 40px;
      font-weight: 500; } }

.booking__description {
  font-size: 14px;
  line-height: 22px;
  font-weight: normal; }
  @media (min-width: 768px) {
    .booking__description {
      max-width: 353px; } }
  @media (min-width: 1024px) {
    .booking__description {
      max-width: 296px;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal; } }
  @media (min-width: 1280px) {
    .booking__description {
      max-width: 408px; } }

.booking__wrapper {
  display: flex;
  justify-content: space-between;
  background-color: #F7F7F5;
  border-radius: 20px;
  padding: 16px;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500; }

.booking__old-price {
  margin: 0; }

.booking__crossed {
  text-decoration: line-through; }

.booking__hire-purchase {
  margin: 12px 0 4px; }

.booking__price {
  font-size: 24px;
  line-height: 32px;
  font-weight: 600; }

.booking__month {
  vertical-align: 8px; }

.booking__count {
  margin: 0; }

.booking__discount {
  position: relative;
  z-index: 1;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center; }

.booking__svg {
  position: absolute;
  z-index: -1; }

.btn {
  display: inline-block;
  margin: 0;
  margin-top: 32px;
  padding: 18px 0;
  box-sizing: border-box;
  background-color: #3D3BFF;
  color: #FFFFFF;
  font-size: 18px;
  line-height: 20px;
  font-weight: 500;
  border-radius: 12px;
  border: 0;
  width: 100%;
  text-align: center;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease, visibility 0.3s ease; }
  @media (min-width: 1024px) {
    .btn {
      width: 50%; } }
  .btn:hover, .btn:focus {
    background-color: #000000; }
  .btn:active {
    opacity: 0.8; }
  .btn:disabled {
    background-color: #EDECEA;
    color: #C3C2BE; }

.form__item {
  position: relative; }
  .form__item:not(:last-of-type) {
    margin-bottom: 16px; }

.form__input {
  background-color: #F7F7F5;
  border: 1px solid #E4E4E4;
  color: #000000;
  padding: 18px;
  width: 100%; }
  .form__input::placeholder {
    color: #000000;
    font-size: 16px;
    line-height: 20px;
    font-weight: normal; }
  .form__input:hover {
    border: 1px solid #C3C2BE; }
  .form__input:focus {
    background-color: #FFFFFF; }
    .form__input:focus + label {
      top: 9px;
      font-size: 11px;
      line-height: 12px;
      font-weight: normal;
      color: #6B6B6B; }
  .form__input:not(:placeholder-shown) + label {
    top: 9px;
    font-size: 11px;
    line-height: 12px;
    font-weight: normal;
    color: #6B6B6B; }
  .form__input:invalid:not(:placeholder-shown) {
    border-color: #FF5733;
    background-color: #FFCDC2; }

.form__label {
  position: absolute;
  left: 19px;
  top: 16px; }

.form__agree {
  font-size: 12px;
  line-height: 16px;
  font-weight: normal;
  color: #6B6B6B;
  margin: 0;
  margin-top: 16px; }
  .form__agree a {
    color: #6B6B6B;
    text-decoration: underline; }

.payment__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 4px; }
  @media (min-width: 1024px) {
    .payment__wrapper {
      gap: 8px; } }

.payment__label {
  font-size: 12px;
  line-height: 16px;
  font-weight: normal;
  outline: 1px solid #E4E4E4;
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 138px;
  min-height: 100px;
  border-radius: 12px;
  cursor: pointer; }
  @media (min-width: 1024px) {
    .payment__label {
      padding: 16px;
      font-size: 14px;
      line-height: 20px;
      font-weight: normal; } }

.payment__input {
  position: fixed;
  opacity: 0; }
  .payment__input:hover + .payment__label,
  .payment__input:focus + .payment__label {
    outline-color: #3D3BFF;
    transition: 0.3s ease; }
  .payment__input:checked + .payment__label {
    outline: 2px solid #3D3BFF;
    transition: none; }

.payment__svg-wrapper {
  margin-top: auto; }

.title {
  font-size: 18px;
  line-height: 24px;
  font-weight: 500; }
  @media (min-width: 1024px) {
    .title {
      font-size: 20px;
      line-height: 26px;
      font-weight: 500; } }
